import React from 'react';
import Select from 'react-select';
import "./AgeSelect.css";
import {useDispatch} from "react-redux";
import {setState} from "../../store/user";

import "./StateSelect.css";

const customStyles = {
    control: (provided, state) => ({
        ...provided,
        backgroundColor: '#F2F4F5',
        color: '#000',
        borderRadius: '30px',
        padding: '4px 5px',
        border: 'none',
        fontFamily: 'Pretendard',
        textAlign: 'center',
    }),
    menuList: (provided) => ({
        ...provided,
        maxHeight: '100px', //     
        overflowY: 'auto', //    
    }),
    option: (provided, state) => ({
        ...provided,
        backgroundColor: state.isFocused ? '#EB604A' : '#fff',
        textAlign: 'center',
        color: '#000',
    })
};

const options = [
    { value: '//', label: '//' },
    { value: '', label: '' },
    { value: '/', label: '/' },
    { value: '', label: '' },
    { value: '/', label: '/' },
    { value: '//', label: '//' },
    { value: '', label: '' },
    { value: '/', label: '/' },
    { value: '', label: '' },
];

const StateSelect = () => {
    let dispatch = useDispatch();


    return (
        <div className="StateSelect">
            <Select className="StateSelector" onChange={(e) => {dispatch(setState(e.value))}} placeholder="" options={options} styles={customStyles} isSearchable={false} components={{IndicatorSeparator: () => null,}}/>
        </div>
    );
};

export default StateSelect;
